'use strict';

(function () {
  var stabilitySlide = document.querySelector('.features__stability');
  var prestigiousnessSlide = document.querySelector('.features__prestigiousness');
  var careerSlide = document.querySelector('.features__career');
  var cultureSlide = document.querySelector('.features__culture');

  var stabilityButton = document.querySelector('.stability');
  var prestigiousnessButton = document.querySelector('.prestigiousness');
  var careerButton = document.querySelector('.career');
  var cultureButton = document.querySelector('.culture');

  var slidsArray = [stabilitySlide, prestigiousnessSlide, careerSlide, cultureSlide];

  var buttonsArray = [stabilityButton, prestigiousnessButton, careerButton, cultureButton];

  for (var i = 0; buttonsArray.length > i; i++) {
    buttonsArray[i].addEventListener('mouseover', function (evt) {
      for (var i = 0; buttonsArray.length > i; i++) {
        slidsArray[i].classList.remove('visible');
        if (evt.target === buttonsArray[i]) {
          slidsArray[i].classList.add('visible');
        }
      }
    });
  }
})();
